@media only screen and (max-width: 1100px) {
  body .navbarHeader {
    font-size: 25px;
    margin: 15px;
  }
  body .navLink {
    padding: 2%;
    font-size: 30px;
  }
  body .menu-footer {
    font-size: 16px;
    margin: 20px;
  }
}

@media only screen and (max-width: 660px) {
  body .navbarHeader {
    font-size: 4.5vw;
    width: 90%;
    font-weight: bold;
  }
  body .navLink {
    padding: 2%;
    font-size: 5vw;
    font-weight: bold;
  }
  body .menu-footer {
    font-size: 3vw;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  height: 100%;
}
.navbarHeader {
  width: 90%;

  font-weight: 600;
  font-family: "laceddays";
  font-size: 30px;
  text-align: left;

  margin: 10px;
}
.navbarList {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
}
.navLink {
  font-weight: 600;
  font-family: "laceddays";
  font-size: 32px;
  text-decoration: none;
  color: var(--text-border-color);
  font-weight: 200;

  padding: 5%;
}
.navLink:hover {
  scale: 1.1;
  color: var(--decorative-color);
}
.navLink.active {
  text-decoration: underline;
  color: var(--decorative-color);
  scale: none;
}
.navLink.active:hover {
  cursor: default;
}
.menu-footer {
  font-size: 18px;
}
